<template>
  <div class="details clearfix">
    <div class="index-content">
      <div class="details-box">
        <div class="details-info">
          <div class="details-name">oldzhao</div>
          <div class="details-time">{{ getTime(detailsData.time) }}</div>
        </div>
        <img class="details-iamges" :src="getImage(detailsData.imgId)" alt="images">
        <div class="quill-editor">
          <div class="ql-container ql-snow">
              <div class="ql-editor" v-html="detailsData.content"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="index-sidebar">2</div>
  </div>
</template>

<script>
import { getDetalisList } from "@/api/index";
import { filterFormatTime } from "@/utils/filter";

import '@/assets/css/quill.core.css'
import '@/assets/css/quill.snow.css'
import '@/assets/css/quill.bubble.css'

export default {
  name: "detail",
  data() {
    return {
      detailsData: {}
    };
  },
  methods: {
    getTime(time) {
      return filterFormatTime(time);
    },
    getImage(id) {
      return "http://localhost:3000/imgUp/file?id=" + id;
    },
    getDetailsData(id) {
      getDetalisList({ id }).then(res => {
        this.detailsData = res.data.data;
      });
    }
  },
  mounted() {
    const id = this.$route.query.id;

    this.getDetailsData(id);
  }
};
</script>

<style>
.ql-container.ql-snow {
    border: none;
}

.details .details-box {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
}

.details .details-name {
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
}

.details .details-time {
  font-size: 14px;
  line-height: 24px;
}

.details .details-iamges {
  width: 100%;
  max-height: 400px;
  display: block;
  margin: 20px 0;
}

.details-content {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  height: 100%;
  margin: 0px;
  position: relative;
}

.details-editor {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 1.42;
  height: 100%;
  outline: none;
  overflow-y: auto;
  padding: 12px 15px;
  -o-tab-size: 4;
  tab-size: 4;
  -moz-tab-size: 4;
  text-align: left;
  white-space: pre-wrap;
  word-wrap: break-word;
}
</style>

