
/**
 *
 * @export
 * @param {*} time 时间戳
 * @returns 年月日时分秒
 */
export function filterFormatTime(time) {
    const date = new Date(parseInt(time));

    const years = date.getFullYear();
    const months = date.getMonth() + 1 >= 10 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1);
    const days = date.getDate() >= 10 ? date.getDate() : '0' + date.getDate();
    const hours = date.getHours() >= 10 ? date.getHours() : '0' + date.getHours();
    const minutes = date.getMinutes() >= 10 ? date.getMinutes() : '0' + date.getMinutes();
    const seconds = date.getSeconds() >= 10 ? date.getSeconds() : '0' + date.getSeconds();

    return years + '年' + months + '月' + days + '日' + ' ' + hours + ':' + minutes + ':' + seconds; 
}


/**
 *
 *
 * @export
 * @param {*} object 对象
 * @returns 过滤空值后的对象
 */
export function filterObj(object) {
    let obj = {};
    
    for(let k in object) {
        if(object[k] || object[k] === 0) {
            obj[k] = object[k];
        }
    }
   
    return obj;
}